<h3>async </h3>
<div class="demo-block">
  <p>titlemetalink</p>
</div>
<demo-block> <div slot="desc">
export default {
    name: 'async',
    metaInfo () {
      return {
        title: this.pageName
      }
    },
    data () {
      return {
        pageName: 'loading'
      }
    },
    mounted () {
      setTimeout(() => {
        this.pageName = 'async'
      }, 2000)
    }
  }
  
</div><div slot="highlight"><pre v-pre=""><code v-pre="" class="language-js">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">name</span>: <span class="hljs-string">'async'</span>,
    metaInfo () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">title</span>: <span class="hljs-keyword">this</span>.pageName
      }
    },
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">pageName</span>: <span class="hljs-string">'loading'</span>
      }
    },
    mounted () {
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        <span class="hljs-keyword">this</span>.pageName = <span class="hljs-string">'async'</span>
      }, <span class="hljs-number">2000</span>)
    }
  }
  
</code></pre>
</div></demo-block>

</section></template>

<script>
  export default {
    metaInfo: {
      title: 'metaInfo', // set a title
      meta: [{                 // set meta
        name: 'keyWords',
        content: 'metaInfo'
      }],
      link: [{                 // set link
        rel: 'asstes',
        href: 'https://xview.github.io/xview/'
      }]
    }
  }
</script>


